import React, {useState} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
// import Link from 'react-router-dom/Link'
import { Redirect } from 'react-router';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
  button1: {
    '&:hover': {
      backgroundColor: '#fff',
      color: '#3c52b2',
  }},
  button2: {
    '&:hover': {
      color: '#fff',
  }},
}));


function ToolBar(props){
    const classes = useStyles();
    const [login, goLogin] = useState(false)
    const [signup, goSignup] = useState(false)

    function handleLogin(){
      goLogin(login => !login)
    }

    function handleSignup(){
      goSignup(signup =>!signup)
    }

    return (
        <AppBar position="static">
            <Toolbar>
                <Typography variant="h6" className={classes.title}>
                    Online Calendar
                </Typography>
                {login && <Redirect push to="/login"/>}
                {signup && <Redirect push to="/signup"/>}
                {!props.isLogged &&
                  <div>    
                    <Button color="inherit" className={classes.button1} onClick={handleLogin}>Login</Button>
                    <Button className={classes.button2} onClick={handleSignup}>SignUp</Button>
                  </div>
                }
                {props.isLogged &&
                  <div>
                    <Button color="inherit" className={classes.button2} onClick={props.logout}>Logout</Button>
                  </div>
                }
            </Toolbar>
        </AppBar>
    );
}

export default ToolBar;